import { Component, OnInit } from '@angular/core';
import { JobPosition } from 'src/app/shared/models/JobPosition';
import { JobService } from 'src/app/shared/services/recruitment/job.service';
import {Observable} from 'rxjs';
import { shareReplay, skipWhile, switchMap, tap } from 'rxjs/operators';
import { CompanyService } from 'src/app/shared/services/company/company.service';
import { MatDialog } from '@angular/material/dialog';
import { DeviceService } from 'src/app/shared/services/client/device.service';
@Component({
  selector: 'app-recruitment-position-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {
  company$: Observable<any> = this.companyService.company$;
  positions$: Observable<any> = this.company$.pipe(
    skipWhile(c => !c),
    switchMap((res:any)=>{
      return this.jobService.getJobPositionsApi({company:res.code}).pipe(
        tap((r:any)=>this.jobService.positions = r)
      )
    }),
    shareReplay(3)
  );
  constructor(private jobService: JobService,
    private deviceService: DeviceService,
    public dialogModule: MatDialog, private companyService: CompanyService) { }

  ngOnInit(): void {
  }
  hasSavedEvent(event:any){
    const swModalQuickCreateQuickCreateLabelClose:any = document.getElementById('swModalQuickCreateQuickCreateLabelClose');
    if(swModalQuickCreateQuickCreateLabelClose){
      swModalQuickCreateQuickCreateLabelClose.click();
    }
    this.deviceService.oSuccessNotification('Position Created', `${event.title} was just created`)
  }
  newPosition(){

  }

}
